import {
  EventEmitter,
  AfterViewInit,
  Output,
  Component,
  Input,
  OnInit,
  ElementRef,
} from '@angular/core';
import { BcTagBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

/**
 * Componente Tag
 *
 * @export
 * @class BcTagComponent
 */
@Component({
  selector: 'bc-tag',
  templateUrl: 'bc-tag.component.html',
  styles: [],
})
export class BcTagComponent implements AfterViewInit, OnInit {
  /**
   * Id del elemento.
   *
   * @type {string}
   * @memberof BcfooterComponent
   */
  @Input() componentId: string;

  /**
   * Icono del botón izquierdo.
   *
   * @memberof BcTagComponent
   */
  @Input() iconLeft = '';

  /**
   * Icono del botón derecho para type tag
   *
   * @memberof BcTagComponent
   */
  @Input() iconRight = '';

  /**
   * Icono '+' del botón derecho para el filtro (indica adición).
   *
   * @memberof BcTagComponent
   */
  @Input() iconAdd = 'true';

  /**
   * Icono 'x' del botón derecho para el filtro (indica eliminación).
   *
   * @memberof BcTagComponent
   */
  @Input() iconDelete = 'true';

  /**
   * Texto principal del elemento.
   *
   * @memberof BcTagComponent
   */
  @Input() textElement = '';

  /**
   * Propiedad de estado que indica si está seleccionado o deshabilitado.
   *
   * @type {('disabled' | 'selected')}
   * @memberof BcTagComponent
   */
  @Input() status: 'disabled' | 'selected';

  /**
   * Propiedad que identifica el tipo de botón que se va a utilizar.
   *
   * @memberof BcTagComponent
   */
  @Input() typeTag: 'button' | 'filter' | 'dropdown' = 'button';

  /**
   * Objeto encargado de llenar las listas con las propiedades definidas.
   *
   * @memberof BcTagComponent
   */
  @Input() values: Array<{ spanText: string; value: any; iconName?: string }>;

  /**
   * Tipo de opciones, por default es el botón primario parametro de entrada 'secondary'.
   *
   * @memberof BcTagComponent
   */
  @Input() typeButtonTag = '';

  /**
   * Texto de accesibilidad del elemento.
   *
   * @memberof BcTagComponent
   */
  @Input() textAriaLabel: string;

  /**
   * Ancho en píxeles(px) del tag.
   *
   * @memberof BcTagComponent
   */
  @Input() widthTag = 'auto';

  /**
   * Ver opcion seleccionada
   *
   * @memberof BcTagComponent
   */
  @Input() showOptionSelected: boolean = true;

  /**
   * Salida del valor actual del componente.
   *
   * @type {(EventEmitter<string | string[]>)}
   * @memberof BcInputSelectComponent
   */
  @Output() valueOutput: EventEmitter<string | string[]> = new EventEmitter<
    string | string[]
  >();

  @Output() onClickButton: EventEmitter<void> = new EventEmitter<void>();

  bcTagBehavior: BcTagBehavior;

  constructor(
    private elementRef: ElementRef,
    private bcHelperService: BcHelperService
  ) {}

  ngOnInit(): void {
    this.componentId = this.componentId ?? this.bcHelperService.getId(this);
  }

  ngAfterViewInit() {
    switch (this.typeTag) {
      case 'button':
        this.initButton();
        break;
      case 'filter':
        this.initFilter();
        break;
      case 'dropdown':
        this.initDropdown();
        break;
    }
  }

  handleClick(event: MouseEvent) {
    if (this.status === 'disabled') {
      event.stopPropagation();
    }
  }

  handleClickButton() {
    this.onClickButton.emit();
  }

  getClassButton() {
    if (this.typeButtonTag === 'secondary') {
      return this.status === 'disabled'
        ? 'bc-tag-secondary-disabled'
        : 'bc-tag-secondary';
    }
    return this.status === 'disabled' ? 'bc-tag-disabled' : 'bc-tag';
  }

  getClassFilter() {
    return this.status === 'disabled'
      ? 'bc-tag-filter-disabled'
      : 'bc-tag-filter';
  }

  private initButton() {
    const buttonElement =
      this.elementRef.nativeElement.querySelector('[role="button"]');
    buttonElement.onclick = this.handleClickButton.bind(this);
    this.bcTagBehavior = new BcTagBehavior();
    this.bcTagBehavior.setId(this.componentId);
    this.bcTagBehavior.setUp();
  }

  private initFilter() {
    this.bcTagBehavior = new BcTagBehavior();
    this.bcTagBehavior.setId(this.componentId);
    this.bcTagBehavior.outputFunction = (data) => {
      this.valueOutput.emit(data);
    };
    this.bcTagBehavior.setUp();
  }

  private initDropdown() {
    this.bcTagBehavior = new BcTagBehavior();
    this.bcTagBehavior.setId(this.componentId);
    this.bcTagBehavior.outputFunction = (data) => {
      this.valueOutput.emit(data);
    };
    this.bcTagBehavior.setItems(this.values);
    this.bcTagBehavior.setUp();
  }
}
